// import logo from './logo.svg';
import './App.css';
import {useState} from 'react'

function App() {
  const [toDos, setToDos] = useState([]);
  const [toDo, setToDo] = useState('');
  // console.log(toDos);
  
  return (
    <div className="app">
      <div className="mainHeading">
        <h1>ToDo List</h1>
      </div>

      <div className="subHeading">
        <br />
        <h2>Whoop, it's Wednesday üåù ‚òï </h2>
      </div>

      <div className="input">
        <input value = {toDo} onChange = {(event) => setToDo(event.target.value)} type="text" placeholder="üñäÔ∏è Add item..." />
        <i onClick = {() =>{
          setToDos([...toDos, {id:Date.now(), text: toDo, status:false}])
          setToDo("")
        }} className="fas fa-plus"></i>
      </div>

      <div className="todos">

        {toDos.map((obj,index)=> {
          return(
            <div className="todo">
            <div className="left">
              <input onChange = {(event)=>{
                console.log(event.target.checked);
                setToDos(toDos.filter((obj2,index)=>{
                  if(obj2.id === obj.id){
                    obj2.status = event.target.checked
                  }
                  console.log(obj2);
                  return obj2
                }))
              }} value = {obj.status} type="checkbox" name="" id="" />
              <p style = {{textDecoration: obj.status ? 'line-through' : 'none  '}}>
                {obj.text}
              </p>
            </div>
            <div className="right">
              <i onClick = {(event)=>{
                console.log(obj.text);
                setToDos(toDos.filter((obj2,index)=>{
                  return(obj2.id !== obj.id)
                }))
              }} className="fas fa-times"></i>
            </div>
          </div>
          )
        })}

        {/* {toDos.map((obj) =>{
          if(obj.status){
            return(
              <h1>{obj.text}</h1>
            )
          }
          return null
        })} */}

      </div>
    </div>
  );
}

export default App;
